"use client";

import { Fragment, useState, useEffect } from "react";
import { Dialog, Transition, Disclosure } from "@headlessui/react";
import { X, ChevronDown, ChevronUp, RotateCcw } from "lucide-react";
import { cn } from "@/lib/utils";

interface FilterDrawerProps {
    isOpen: boolean;
    onClose: () => void;
    filters: any;
    setFilters: (filters: any) => void;
    onReset: () => void;
}

export default function FilterDrawer({
    isOpen,
    onClose,
    filters,
    setFilters,
    onReset,
}: FilterDrawerProps) {
    // Persist filters to localStorage
    useEffect(() => {
        const saved = localStorage.getItem("stockFilters");
        if (saved) {
            try {
                const parsed = JSON.parse(saved);
                // Only load if not empty to avoid overwriting initial state if needed
                if (Object.keys(parsed).length > 0) {
                    // We don't auto-set here to avoid infinite loops or hydration mismatch
                    // But we could offer a "Load Saved" or just init state in parent.
                    // For now, let's just save when changed.
                }
            } catch (e) {
                console.error("Failed to load filters", e);
            }
        }
    }, []);

    useEffect(() => {
        localStorage.setItem("stockFilters", JSON.stringify(filters));
    }, [filters]);

    const handleInputChange = (key: string, value: any) => {
        setFilters((prev: any) => {
            const next = { ...prev, [key]: value };
            if (value === "" || value === null) delete next[key];
            return next;
        });
    };

    const handleCheckboxChange = (key: string, checked: boolean) => {
        setFilters((prev: any) => {
            const next = { ...prev, [key]: checked };
            if (!checked) delete next[key];
            return next;
        });
    };

    const FilterSection = ({ title, children, defaultOpen = true }: { title: string, children: React.ReactNode, defaultOpen?: boolean }) => (
        <Disclosure defaultOpen={defaultOpen}>
            {({ open }) => (
                <div className="border-b border-gray-200 dark:border-slate-700 py-4">
                    <Disclosure.Button className="flex w-full justify-between items-center text-left text-sm font-medium text-gray-900 dark:text-white focus:outline-none">
                        <span>{title}</span>
                        {open ? <ChevronUp className="h-4 w-4 text-gray-500" /> : <ChevronDown className="h-4 w-4 text-gray-500" />}
                    </Disclosure.Button>
                    <Disclosure.Panel className="pt-4 space-y-4">
                        {children}
                    </Disclosure.Panel>
                </div>
            )}
        </Disclosure>
    );

    return (
        <Transition.Root show={isOpen} as={Fragment}>
            <Dialog as="div" className="relative z-50" onClose={onClose}>
                <Transition.Child
                    as={Fragment}
                    enter="ease-in-out duration-300"
                    enterFrom="opacity-0"
                    enterTo="opacity-100"
                    leave="ease-in-out duration-300"
                    leaveFrom="opacity-100"
                    leaveTo="opacity-0"
                >
                    <div className="fixed inset-0 bg-black/50 transition-opacity" />
                </Transition.Child>

                <div className="fixed inset-0 overflow-hidden">
                    <div className="absolute inset-0 overflow-hidden">
                        <div className="pointer-events-none fixed inset-y-0 left-0 flex max-w-full pr-10">
                            <Transition.Child
                                as={Fragment}
                                enter="transform transition ease-in-out duration-300 sm:duration-500"
                                enterFrom="-translate-x-full"
                                enterTo="translate-x-0"
                                leave="transform transition ease-in-out duration-300 sm:duration-500"
                                leaveFrom="translate-x-0"
                                leaveTo="-translate-x-full"
                            >
                                <Dialog.Panel className="pointer-events-auto w-screen max-w-md">
                                    <div className="flex h-full flex-col overflow-y-scroll bg-white dark:bg-slate-900 shadow-xl">
                                        <div className="px-4 py-6 sm:px-6 border-b border-gray-200 dark:border-slate-700">
                                            <div className="flex items-start justify-between">
                                                <Dialog.Title className="text-lg font-semibold text-gray-900 dark:text-white">
                                                    Advanced Filters
                                                </Dialog.Title>
                                                <div className="ml-3 flex h-7 items-center">
                                                    <button
                                                        type="button"
                                                        className="relative -m-2 p-2 text-gray-400 hover:text-gray-500"
                                                        onClick={onClose}
                                                    >
                                                        <span className="absolute -inset-0.5" />
                                                        <span className="sr-only">Close panel</span>
                                                        <X className="h-6 w-6" aria-hidden="true" />
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="relative flex-1 px-4 py-6 sm:px-6">

                                            {/* Search */}
                                            <div className="mb-6">
                                                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                                                    Search Symbol
                                                </label>
                                                <input
                                                    type="text"
                                                    placeholder="e.g. RELIANCE"
                                                    className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded-md px-3 py-2 text-sm text-gray-900 dark:text-white focus:ring-2 focus:ring-blue-500 outline-none"
                                                    value={filters.search || ""}
                                                    onChange={(e) => handleInputChange("search", e.target.value)}
                                                />
                                            </div>

                                            {/* Sections */}
                                            <FilterSection title="Market Data">
                                                <div className="grid grid-cols-2 gap-4">
                                                    <div>
                                                        <label className="block text-xs text-gray-500 dark:text-gray-400 mb-1">Min Price</label>
                                                        <input
                                                            type="number"
                                                            className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded px-2 py-1 text-sm text-gray-900 dark:text-white outline-none"
                                                            value={filters.min_price || ""}
                                                            onChange={(e) => handleInputChange("min_price", e.target.value)}
                                                        />
                                                    </div>
                                                    <div>
                                                        <label className="block text-xs text-gray-500 dark:text-gray-400 mb-1">Max Price</label>
                                                        <input
                                                            type="number"
                                                            className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded px-2 py-1 text-sm text-gray-900 dark:text-white outline-none"
                                                            value={filters.max_price || ""}
                                                            onChange={(e) => handleInputChange("max_price", e.target.value)}
                                                        />
                                                    </div>
                                                </div>

                                                <div className="mt-4">
                                                    <label className="block text-xs text-gray-500 dark:text-gray-400 mb-1">Sector</label>
                                                    <select
                                                        className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded px-2 py-1 text-sm text-gray-900 dark:text-white outline-none"
                                                        value={filters.sector || ""}
                                                        onChange={(e) => handleInputChange("sector", e.target.value)}
                                                    >
                                                        <option value="">All Sectors</option>
                                                        <option value="Nifty 50">Nifty 50</option>
                                                        <option value="Bank Nifty">Bank Nifty</option>
                                                        <option value="IT">IT</option>
                                                        <option value="Auto">Auto</option>
                                                        <option value="Pharma">Pharma</option>
                                                        <option value="FMCG">FMCG</option>
                                                        <option value="Metals">Metals</option>
                                                        <option value="Energy">Energy</option>
                                                    </select>
                                                </div>
                                            </FilterSection>

                                            <FilterSection title="Performance (3-Day)">
                                                <div className="grid grid-cols-2 gap-4">
                                                    <div>
                                                        <label className="block text-xs text-gray-500 dark:text-gray-400 mb-1">Min Avg %</label>
                                                        <input
                                                            type="number"
                                                            step="0.1"
                                                            className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded px-2 py-1 text-sm text-gray-900 dark:text-white outline-none"
                                                            value={filters.min_avg_3day_pct || ""}
                                                            onChange={(e) => handleInputChange("min_avg_3day_pct", e.target.value)}
                                                        />
                                                    </div>
                                                    <div>
                                                        <label className="block text-xs text-gray-500 dark:text-gray-400 mb-1">Max Avg %</label>
                                                        <input
                                                            type="number"
                                                            step="0.1"
                                                            className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded px-2 py-1 text-sm text-gray-900 dark:text-white outline-none"
                                                            value={filters.max_avg_3day_pct || ""}
                                                            onChange={(e) => handleInputChange("max_avg_3day_pct", e.target.value)}
                                                        />
                                                    </div>
                                                </div>
                                            </FilterSection>

                                            <FilterSection title="Quick Filters">
                                                <div className="space-y-3">
                                                    <label className="flex items-center gap-2 cursor-pointer">
                                                        <input
                                                            type="checkbox"
                                                            className="rounded bg-gray-200 dark:bg-slate-700 border-gray-300 dark:border-slate-600 text-blue-600"
                                                            checked={filters.constant_only || false}
                                                            onChange={(e) => handleCheckboxChange("constant_only", e.target.checked)}
                                                        />
                                                        <span className="text-sm text-gray-700 dark:text-gray-300">Constant Price (Stability)</span>
                                                    </label>
                                                    <label className="flex items-center gap-2 cursor-pointer">
                                                        <input
                                                            type="checkbox"
                                                            className="rounded bg-gray-200 dark:bg-slate-700 border-gray-300 dark:border-slate-600 text-blue-600"
                                                            checked={filters.gainers_only || false}
                                                            onChange={(e) => handleCheckboxChange("gainers_only", e.target.checked)}
                                                        />
                                                        <span className="text-sm text-gray-700 dark:text-gray-300">Gainers Only</span>
                                                    </label>
                                                    <label className="flex items-center gap-2 cursor-pointer">
                                                        <input
                                                            type="checkbox"
                                                            className="rounded bg-gray-200 dark:bg-slate-700 border-gray-300 dark:border-slate-600 text-blue-600"
                                                            checked={filters.losers_only || false}
                                                            onChange={(e) => handleCheckboxChange("losers_only", e.target.checked)}
                                                        />
                                                        <span className="text-sm text-gray-700 dark:text-gray-300">Losers Only</span>
                                                    </label>
                                                    <label className="flex items-center gap-2 cursor-pointer">
                                                        <input
                                                            type="checkbox"
                                                            className="rounded bg-gray-200 dark:bg-slate-700 border-gray-300 dark:border-slate-600 text-blue-600"
                                                            checked={filters.high_volume_only || false}
                                                            onChange={(e) => handleCheckboxChange("high_volume_only", e.target.checked)}
                                                        />
                                                        <span className="text-sm text-gray-700 dark:text-gray-300">High Volume Only</span>
                                                    </label>
                                                </div>
                                            </FilterSection>

                                            <FilterSection title="Technical Indicators">
                                                <div className="space-y-4">
                                                    <div>
                                                        <label className="block text-xs text-gray-500 dark:text-gray-400 mb-1">MACD Status</label>
                                                        <select
                                                            className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded px-2 py-1 text-sm text-gray-900 dark:text-white outline-none"
                                                            value={filters.macd_status || ""}
                                                            onChange={(e) => handleInputChange("macd_status", e.target.value)}
                                                        >
                                                            <option value="">Any</option>
                                                            <option value="above_zero">Above Zero (Bullish)</option>
                                                            <option value="below_zero">Below Zero (Bearish)</option>
                                                            <option value="near_zero">Near Zero (Neutral)</option>
                                                        </select>
                                                    </div>
                                                    <div>
                                                        <label className="block text-xs text-gray-500 dark:text-gray-400 mb-1">RSI Zone</label>
                                                        <select
                                                            className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded px-2 py-1 text-sm text-gray-900 dark:text-white outline-none"
                                                            value={filters.rsi_zone || ""}
                                                            onChange={(e) => handleInputChange("rsi_zone", e.target.value)}
                                                        >
                                                            <option value="">Any</option>
                                                            <option value="overbought">Overbought</option>
                                                            <option value="oversold">Oversold</option>
                                                            <option value="neutral">Neutral</option>
                                                        </select>
                                                    </div>
                                                    <div>
                                                        <label className="block text-xs text-gray-500 dark:text-gray-400 mb-1">Strength</label>
                                                        <select
                                                            className="w-full bg-gray-50 dark:bg-slate-800 border border-gray-200 dark:border-slate-700 rounded px-2 py-1 text-sm text-gray-900 dark:text-white outline-none"
                                                            value={filters.strength || ""}
                                                            onChange={(e) => handleInputChange("strength", e.target.value)}
                                                        >
                                                            <option value="">Any</option>
                                                            <option value="buyers">Buyers Dominating</option>
                                                            <option value="sellers">Sellers Dominating</option>
                                                            <option value="balanced">Balanced</option>
                                                        </select>
                                                    </div>
                                                </div>
                                            </FilterSection>
                                        </div>

                                        <div className="border-t border-gray-200 dark:border-slate-700 px-4 py-6 sm:px-6">
                                            <button
                                                type="button"
                                                className="flex w-full items-center justify-center rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-red-600"
                                                onClick={onReset}
                                            >
                                                <RotateCcw className="w-4 h-4 mr-2" />
                                                Reset All Filters
                                            </button>
                                        </div>
                                    </div>
                                </Dialog.Panel>
                            </Transition.Child>
                        </div>
                    </div>
                </div>
            </Dialog>
        </Transition.Root>
    );
}
