"use client";

import { Stock, useWatchlist, useToggleWatchlist } from "@/hooks/useStocks";
import { ArrowDown, ArrowUp, Minus, Star, Activity, Zap } from "lucide-react";
import { cn } from "@/lib/utils";
import { useState, useMemo } from "react";
import { useRouter } from "next/navigation";
import AutoFilter, { FilterState } from "./AutoFilter";
import { applyColumnFilters } from "@/utils/globalFilter";

interface StocksTableProps {
    stocks: Stock[];
    isLoading: boolean;
    simpleMode?: boolean;
    variant?: "card" | "plain";
}

// Helper to get unique values for a column
const getUniqueValues = (data: any[], key: string): string[] => {
    const values = new Set<string>();
    data.forEach(item => {
        const val = key.split('.').reduce((o, i) => o?.[i], item);
        if (val) values.add(String(val));
    });
    return Array.from(values).sort();
};

export default function StocksTable({ stocks, isLoading, simpleMode = false, variant = "card" }: StocksTableProps) {
    const router = useRouter();
    const [displayCount, setDisplayCount] = useState(50);

    // Client-side state
    const [columnFilters, setColumnFilters] = useState<Record<string, FilterState>>({});
    const [sortConfig, setSortConfig] = useState<{ key: string; direction: "asc" | "desc" }>({
        key: "history.avg_3_day_change_pct",
        direction: "asc"
    });

    const { data: watchlist } = useWatchlist();
    const { mutate: toggleWatchlist } = useToggleWatchlist();

    const isInWatchlist = (symbol: string) => {
        return watchlist?.some(s => s.symbol === symbol);
    };

    const handleToggleWatchlist = (symbol: string) => {
        const action = isInWatchlist(symbol) ? "remove" : "add";
        toggleWatchlist({ symbol, action });
    };

    const handleLoadMore = () => {
        setDisplayCount(prev => prev + 50);
    };

    const handleRowClick = (symbol: string) => {
        router.push(`/stocks/${symbol}`);
    };

    const handleFilterChange = (key: string, filter: FilterState | null) => {
        setColumnFilters(prev => {
            const next = { ...prev };
            if (filter === null) {
                delete next[key];
            } else {
                next[key] = filter;
            }
            return next;
        });
    };

    const handleSort = (key: string, direction: "asc" | "desc") => {
        setSortConfig({ key, direction });
    };

    // Derived Data & Filtering
    const processedStocks = useMemo(() => {
        if (!stocks) return [];

        const enriched = stocks.map(stock => {
            // 3D Average Strength Logic
            let strength3D = "Neutral";
            if (stock.history.avg_3_day_change_pct > 0.5) strength3D = "Strong Buyers";
            else if (stock.history.avg_3_day_change_pct < -0.5) strength3D = "Strong Sellers";

            // Indicators Composite
            const indicatorsList = [];
            if (stock.indicators.macd_status === "above_zero") indicatorsList.push("MACD Above Zero (Bullish)");
            if (stock.indicators.macd_status === "below_zero") indicatorsList.push("MACD Below Zero (Bearish)");
            if (stock.indicators.macd_status === "near_zero") indicatorsList.push("MACD Near Zero (Neutral)");

            if (stock.indicators.rsi_status === "overbought") indicatorsList.push("RSI Overbought");
            if (stock.indicators.rsi_status === "oversold") indicatorsList.push("RSI Oversold");
            if (stock.indicators.rsi_status === "neutral") indicatorsList.push("RSI Neutral");

            return {
                ...stock,
                derived: {
                    strength3D,
                    indicatorsList,
                    macdLabel: stock.indicators.macd_status === "above_zero" ? "Above" :
                        stock.indicators.macd_status === "below_zero" ? "Below" : "Neutral",
                    rsiLabel: stock.indicators.rsi_status === "overbought" ? "Over" :
                        stock.indicators.rsi_status === "oversold" ? "Under" : "Neutral",
                    strengthLabel: stock.indicators.strength_label === "buyers" ? "Buyers Dominating" :
                        stock.indicators.strength_label === "sellers" ? "Sellers Dominating" : "Balanced"
                }
            };
        });

        // 2. Apply Column Filters
        const filtered = applyColumnFilters(enriched, columnFilters);

        // 3. Sort
        return filtered.sort((a, b) => {
            const { key, direction } = sortConfig;
            let valA: any, valB: any;

            // Special case for default sort
            if (key === "history.avg_3_day_change_pct") {
                // Sort by abs(avg_3_day_change_pct)
                valA = Math.abs(a.history.avg_3_day_change_pct);
                valB = Math.abs(b.history.avg_3_day_change_pct);
            } else {
                // Generic accessor
                valA = key.split('.').reduce((o, i) => o?.[i], a);
                valB = key.split('.').reduce((o, i) => o?.[i], b);

                // Handle derived
                if (key.startsWith("derived.")) {
                    valA = key.split('.').reduce((o, i) => o?.[i], a);
                    valB = key.split('.').reduce((o, i) => o?.[i], b);
                }
            }

            if (valA < valB) return direction === "asc" ? -1 : 1;
            if (valA > valB) return direction === "asc" ? 1 : -1;
            return 0;
        });

    }, [stocks, columnFilters, sortConfig]);

    const displayedStocks = processedStocks.slice(0, displayCount);

    // Generate options for filters
    const getOptions = (key: string) => {
        if (!processedStocks.length) return [];
        if (key === "derived.indicatorsList") {
            // Flatten all indicators
            const all = new Set<string>();
            processedStocks.forEach(s => s.derived.indicatorsList.forEach((i: string) => all.add(i)));
            return Array.from(all).sort();
        }
        return getUniqueValues(processedStocks, key);
    };

    // Helper for % colors
    const getPctColor = (val: number) => {
        if (val > 0) return "text-green-600 dark:text-green-400";
        if (val < 0) return "text-red-600 dark:text-red-400";
        return "text-gray-400 dark:text-gray-500";
    };

    if (isLoading) {
        return <div className="text-center py-20 text-gray-500 dark:text-gray-400">Loading market data...</div>;
    }

    return (
        <>
            <div className={cn(
                "w-full overflow-x-auto",
                variant === "card" && "rounded-2xl border border-gray-200 dark:border-slate-700 bg-white dark:bg-slate-800 shadow-sm dark:shadow-md min-h-[600px]"
            )}>
                <table className="min-w-full table-auto border-separate border-spacing-y-1 text-sm text-left relative">
                    <thead className="text-xs text-gray-600 dark:text-gray-300 uppercase bg-gray-50 dark:bg-slate-800 sticky top-0 z-20 backdrop-blur-sm">
                        <tr>
                            {!simpleMode && (
                                <th className="px-3 py-2 min-w-[80px] border-b border-gray-200 dark:border-slate-700">
                                    <div className="flex items-center justify-between gap-2">
                                        <span>Rank</span>
                                        <AutoFilter
                                            columnKey="rank"
                                            title="Rank"
                                            type="number"
                                            onFilter={(f) => handleFilterChange("rank", f)}
                                            currentFilter={columnFilters["rank"] || null}
                                            onSort={(d) => handleSort("rank", d)}
                                            sortDirection={sortConfig.key === "rank" ? sortConfig.direction : null}
                                        />
                                    </div>
                                </th>
                            )}
                            <th className="px-3 py-2 w-40 sticky left-0 z-30 bg-gray-50 dark:bg-slate-800 border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-between gap-2">
                                    <span>Symbol</span>
                                    <AutoFilter
                                        columnKey="symbol"
                                        title="Symbol"
                                        type="text"
                                        options={getOptions("symbol")}
                                        onFilter={(f) => handleFilterChange("symbol", f)}
                                        currentFilter={columnFilters["symbol"] || null}
                                        onSort={(d) => handleSort("symbol", d)}
                                        sortDirection={sortConfig.key === "symbol" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            <th className="px-3 py-2 min-w-[100px] text-right border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-end gap-2">
                                    <span>Price</span>
                                    <AutoFilter
                                        columnKey="current_price"
                                        title="Price"
                                        type="number"
                                        onFilter={(f) => handleFilterChange("current_price", f)}
                                        currentFilter={columnFilters["current_price"] || null}
                                        onSort={(d) => handleSort("current_price", d)}
                                        sortDirection={sortConfig.key === "current_price" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            {!simpleMode && (
                                <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                    <div className="flex items-center justify-center gap-2">
                                        <span>Change %</span>
                                        <AutoFilter
                                            columnKey="current_change_pct"
                                            title="Change %"
                                            type="number"
                                            onFilter={(f) => handleFilterChange("current_change_pct", f)}
                                            currentFilter={columnFilters["current_change_pct"] || null}
                                            onSort={(d) => handleSort("current_change_pct", d)}
                                            sortDirection={sortConfig.key === "current_change_pct" ? sortConfig.direction : null}
                                        />
                                    </div>
                                </th>
                            )}
                            {!simpleMode && (
                                <>
                                    <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                        <div className="flex items-center justify-center gap-2">
                                            <span>Day 1 %</span>
                                            <AutoFilter
                                                columnKey="history.day_1_change_pct"
                                                title="Day 1 %"
                                                type="number"
                                                onFilter={(f) => handleFilterChange("history.day_1_change_pct", f)}
                                                currentFilter={columnFilters["history.day_1_change_pct"] || null}
                                                onSort={(d) => handleSort("history.day_1_change_pct", d)}
                                                sortDirection={sortConfig.key === "history.day_1_change_pct" ? sortConfig.direction : null}
                                            />
                                        </div>
                                    </th>
                                    <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                        <div className="flex items-center justify-center gap-2">
                                            <span>Day 2 %</span>
                                            <AutoFilter
                                                columnKey="history.day_2_change_pct"
                                                title="Day 2 %"
                                                type="number"
                                                onFilter={(f) => handleFilterChange("history.day_2_change_pct", f)}
                                                currentFilter={columnFilters["history.day_2_change_pct"] || null}
                                                onSort={(d) => handleSort("history.day_2_change_pct", d)}
                                                sortDirection={sortConfig.key === "history.day_2_change_pct" ? sortConfig.direction : null}
                                            />
                                        </div>
                                    </th>
                                    <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                        <div className="flex items-center justify-center gap-2">
                                            <span>Day 3 %</span>
                                            <AutoFilter
                                                columnKey="history.day_3_change_pct"
                                                title="Day 3 %"
                                                type="number"
                                                onFilter={(f) => handleFilterChange("history.day_3_change_pct", f)}
                                                currentFilter={columnFilters["history.day_3_change_pct"] || null}
                                                onSort={(d) => handleSort("history.day_3_change_pct", d)}
                                                sortDirection={sortConfig.key === "history.day_3_change_pct" ? sortConfig.direction : null}
                                            />
                                        </div>
                                    </th>
                                </>
                            )}
                            <th className="px-3 py-2 w-28 text-center border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-center gap-2">
                                    <span>3-Day Avg</span>
                                    <AutoFilter
                                        columnKey="history.avg_3_day_change_pct"
                                        title="3-Day Avg %"
                                        type="number"
                                        onFilter={(f) => handleFilterChange("history.avg_3_day_change_pct", f)}
                                        currentFilter={columnFilters["history.avg_3_day_change_pct"] || null}
                                        onSort={(d) => handleSort("history.avg_3_day_change_pct", d)}
                                        sortDirection={sortConfig.key === "history.avg_3_day_change_pct" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            {!simpleMode && (
                                <>
                                    <th className="px-3 py-2 w-40 text-center border-b border-gray-200 dark:border-slate-700">
                                        <div className="flex items-center justify-center gap-2">
                                            <span>Indicators</span>
                                            <AutoFilter
                                                columnKey="derived.indicatorsList"
                                                title="Indicators"
                                                type="enum"
                                                options={[
                                                    "MACD Above Zero (Bullish)",
                                                    "MACD Below Zero (Bearish)",
                                                    "MACD Near Zero (Neutral)",
                                                    "RSI Overbought",
                                                    "RSI Oversold",
                                                    "RSI Neutral"
                                                ]}
                                                onFilter={(f) => handleFilterChange("derived.indicatorsList", f)}
                                                currentFilter={columnFilters["derived.indicatorsList"] || null}
                                                onSort={(d) => handleSort("derived.indicatorsList", d)}
                                                sortDirection={sortConfig.key === "derived.indicatorsList" ? sortConfig.direction : null}
                                            />
                                        </div>
                                    </th>
                                    <th className="px-3 py-2 w-16 text-center border-b border-gray-200 dark:border-slate-700">
                                        <div className="flex items-center justify-center gap-2">
                                            <span>M</span>
                                            <AutoFilter
                                                columnKey="derived.macdLabel"
                                                title="MACD Status"
                                                type="enum"
                                                options={["Above", "Below", "Neutral"]}
                                                onFilter={(f) => handleFilterChange("derived.macdLabel", f)}
                                                currentFilter={columnFilters["derived.macdLabel"] || null}
                                                onSort={(d) => handleSort("derived.macdLabel", d)}
                                                sortDirection={sortConfig.key === "derived.macdLabel" ? sortConfig.direction : null}
                                            />
                                        </div>
                                    </th>
                                    <th className="px-3 py-2 w-16 text-center border-b border-gray-200 dark:border-slate-700">
                                        <div className="flex items-center justify-center gap-2">
                                            <span>R</span>
                                            <AutoFilter
                                                columnKey="derived.rsiLabel"
                                                title="RSI Status"
                                                type="enum"
                                                options={["Over", "Under", "Neutral"]}
                                                onFilter={(f) => handleFilterChange("derived.rsiLabel", f)}
                                                currentFilter={columnFilters["derived.rsiLabel"] || null}
                                                onSort={(d) => handleSort("derived.rsiLabel", d)}
                                                sortDirection={sortConfig.key === "derived.rsiLabel" ? sortConfig.direction : null}
                                            />
                                        </div>
                                    </th>
                                    <th className="px-3 py-2 w-40 text-center border-b border-gray-200 dark:border-slate-700">
                                        <div className="flex items-center justify-center gap-2">
                                            <span>Strength</span>
                                            <AutoFilter
                                                columnKey="derived.strengthLabel"
                                                title="Current Strength"
                                                type="enum"
                                                options={["Buyers Dominating", "Sellers Dominating", "Balanced"]}
                                                onFilter={(f) => handleFilterChange("derived.strengthLabel", f)}
                                                currentFilter={columnFilters["derived.strengthLabel"] || null}
                                                onSort={(d) => handleSort("derived.strengthLabel", d)}
                                                sortDirection={sortConfig.key === "derived.strengthLabel" ? sortConfig.direction : null}
                                            />
                                        </div>
                                    </th>
                                    <th className="px-3 py-2 w-12 text-center border-b border-gray-200 dark:border-slate-700">Action</th>
                                </>
                            )}
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200 dark:divide-slate-700">
                        {displayedStocks.map((stock) => (
                            <tr
                                key={stock.symbol}
                                className="hover:bg-gray-100 dark:hover:bg-slate-700/50 transition-colors group cursor-pointer border-b border-gray-200 dark:border-slate-700 last:border-0"
                                onClick={() => handleRowClick(stock.symbol)}
                            >
                                {!simpleMode && <td className="px-3 py-2 font-medium text-gray-700 dark:text-gray-300 text-center">#{stock.rank}</td>}
                                <td className="px-3 py-2 sticky left-0 z-10 bg-white dark:bg-slate-800 group-hover:bg-gray-100 dark:group-hover:bg-slate-700/50 border-r border-gray-200 dark:border-slate-700 whitespace-nowrap">
                                    <div className="font-bold text-gray-900 dark:text-gray-100">{stock.symbol}</div>
                                    <div className="text-[10px] text-gray-500 dark:text-gray-400">{stock.sector}</div>
                                </td>
                                <td className="px-3 py-2 font-mono text-gray-900 dark:text-gray-100 text-right whitespace-nowrap">{stock.current_price.toFixed(2)}</td>
                                {!simpleMode && (
                                    <td className={cn("px-3 py-2 font-medium text-center whitespace-nowrap", getPctColor(stock.current_change_pct))}>
                                        <div className="flex items-center justify-center gap-1">
                                            {stock.current_change_pct > 0 ? <ArrowUp className="w-3 h-3" /> : stock.current_change_pct < 0 ? <ArrowDown className="w-3 h-3" /> : <Minus className="w-3 h-3" />}
                                            {stock.current_change_pct.toFixed(2)}%
                                        </div>
                                    </td>
                                )}
                                {!simpleMode && (
                                    <>
                                        <td className={cn("px-3 py-2 text-xs text-center whitespace-nowrap", getPctColor(stock.history.day_1_change_pct))}>
                                            {stock.history.day_1_change_pct.toFixed(2)}%
                                        </td>
                                        <td className={cn("px-3 py-2 text-xs text-center whitespace-nowrap", getPctColor(stock.history.day_2_change_pct))}>
                                            {stock.history.day_2_change_pct.toFixed(2)}%
                                        </td>
                                        <td className={cn("px-3 py-2 text-xs text-center whitespace-nowrap", getPctColor(stock.history.day_3_change_pct))}>
                                            {stock.history.day_3_change_pct.toFixed(2)}%
                                        </td>
                                    </>
                                )}
                                <td className={cn("px-3 py-2 font-medium text-center whitespace-nowrap", getPctColor(stock.history.avg_3_day_change_pct))}>
                                    <div className="flex items-center justify-center gap-1">
                                        {stock.history.avg_3_day_change_pct > 0 ? <ArrowUp className="w-3 h-3" /> : stock.history.avg_3_day_change_pct < 0 ? <ArrowDown className="w-3 h-3" /> : <Minus className="w-3 h-3" />}
                                        {stock.history.avg_3_day_change_pct.toFixed(2)}%
                                    </div>
                                </td>
                                {!simpleMode && (
                                    <>
                                        <td className="px-3 py-2 whitespace-nowrap text-[10px] text-center text-gray-500 dark:text-gray-400">
                                            <div className="flex flex-col items-center gap-0.5">
                                                <span className={cn(stock.derived.macdLabel === "Above" ? "text-green-600 dark:text-green-400" : stock.derived.macdLabel === "Below" ? "text-red-600 dark:text-red-400" : "text-gray-500 dark:text-gray-400")}>
                                                    MACD: {stock.derived.macdLabel}
                                                </span>
                                                <span className={cn(stock.derived.rsiLabel === "Under" ? "text-green-600 dark:text-green-400" : stock.derived.rsiLabel === "Over" ? "text-red-600 dark:text-red-400" : "text-gray-500 dark:text-gray-400")}>
                                                    RSI: {stock.derived.rsiLabel}
                                                </span>
                                            </div>
                                        </td>
                                        <td className="px-3 py-2 whitespace-nowrap text-center">
                                            <Activity className={cn("w-4 h-4 mx-auto", stock.derived.macdLabel === "Above" ? "text-green-500 dark:text-green-400" : stock.derived.macdLabel === "Below" ? "text-red-500 dark:text-red-400" : "text-gray-400 dark:text-gray-500")} />
                                        </td>
                                        <td className="px-3 py-2 whitespace-nowrap text-center">
                                            <Zap className={cn("w-4 h-4 mx-auto", stock.derived.rsiLabel === "Under" ? "text-green-500 dark:text-green-400" : stock.derived.rsiLabel === "Over" ? "text-red-500 dark:text-red-400" : "text-blue-400 dark:text-blue-500")} />
                                        </td>
                                        <td className="px-3 py-2 whitespace-nowrap">
                                            <div className="flex flex-col gap-1 w-full">
                                                <div className="flex h-1.5 w-full rounded-full overflow-hidden bg-gray-200 dark:bg-slate-700">
                                                    <div className="bg-green-500 dark:bg-green-400 h-full transition-all duration-300" style={{ width: `${stock.indicators.buyer_strength_score}%` }} />
                                                    <div className="bg-red-500 dark:bg-red-400 h-full transition-all duration-300" style={{ width: `${stock.indicators.seller_strength_score}%` }} />
                                                </div>
                                                <div className="flex justify-between text-[10px] font-medium leading-none">
                                                    <span className={cn("text-green-600 dark:text-green-400", stock.indicators.buyer_strength_score > stock.indicators.seller_strength_score ? "font-bold" : "opacity-70")}>
                                                        {stock.indicators.buyer_strength_score > stock.indicators.seller_strength_score ? "Buyers" : ""}
                                                    </span>
                                                    <span className={cn("text-red-600 dark:text-red-400", stock.indicators.seller_strength_score > stock.indicators.buyer_strength_score ? "font-bold" : "opacity-70")}>
                                                        {stock.indicators.seller_strength_score > stock.indicators.buyer_strength_score ? "Sellers" : ""}
                                                    </span>
                                                </div>
                                            </div>
                                        </td>
                                        <td className="px-3 py-2 text-center whitespace-nowrap">
                                            <button
                                                className="p-1.5 hover:bg-gray-200 dark:hover:bg-slate-600 rounded-full transition-colors text-gray-400 dark:text-gray-500 hover:text-yellow-500 dark:hover:text-yellow-400"
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    handleToggleWatchlist(stock.symbol);
                                                }}
                                            >
                                                <Star className={cn("w-4 h-4", isInWatchlist(stock.symbol) ? "fill-yellow-500 text-yellow-500 dark:text-yellow-400 dark:fill-yellow-400" : "")} />
                                            </button>
                                        </td>
                                    </>
                                )}
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            {!simpleMode && processedStocks.length > displayCount && (
                <div className="mt-4 flex justify-center">
                    <button
                        onClick={handleLoadMore}
                        className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-full text-sm font-medium transition-colors shadow-sm"
                    >
                        Load More Stocks
                    </button>
                </div>
            )}
        </>
    );
}
