"use client";

import { TrendingUp, TrendingDown, BarChart3, X } from "lucide-react";
import { motion } from "framer-motion";
import { cn } from "@/lib/utils";

interface QuickFiltersToolbarProps {
    filters: any;
    setFilters: (filters: any) => void;
    onReset?: () => void;
    className?: string;
}

export default function QuickFiltersToolbar({ filters, setFilters, onReset, className }: QuickFiltersToolbarProps) {

    const FilterPill = ({ label, active, onClick, icon: Icon }: any) => (
        <button
            onClick={onClick}
            className={cn(
                "relative px-4 py-2 rounded-full text-sm font-medium transition-all duration-200 flex items-center gap-2 whitespace-nowrap",
                active
                    ? "text-white shadow-md"
                    : "text-gray-600 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-slate-700"
            )}
        >
            {active && (
                <motion.div
                    layoutId="activePill"
                    className="absolute inset-0 bg-blue-600 rounded-full"
                    transition={{ type: "spring", bounce: 0.2, duration: 0.6 }}
                />
            )}
            <span className="relative z-10 flex items-center gap-2">
                {Icon && <Icon className="w-4 h-4" />}
                {label}
            </span>
        </button>
    );

    return (
        <div className={cn("flex flex-col sm:flex-row items-center justify-between gap-4 p-4 border-b border-gray-200 dark:border-slate-700", className)}>
            <div className="flex flex-wrap gap-2 p-1.5 bg-gray-50 dark:bg-slate-900/50 rounded-full border border-gray-100 dark:border-slate-700/50 w-full sm:w-auto overflow-x-auto">
                <FilterPill
                    label="All Stocks"
                    active={!filters.gainers_only && !filters.losers_only && !filters.high_volume_only}
                    onClick={() => setFilters((p: any) => {
                        const { gainers_only, losers_only, high_volume_only, ...rest } = p;
                        return rest;
                    })}
                />
                <FilterPill
                    label="Gainers"
                    icon={TrendingUp}
                    active={filters.gainers_only}
                    onClick={() => setFilters((p: any) => ({ ...p, gainers_only: !p.gainers_only, losers_only: false }))}
                />
                <FilterPill
                    label="Losers"
                    icon={TrendingDown}
                    active={filters.losers_only}
                    onClick={() => setFilters((p: any) => ({ ...p, losers_only: !p.losers_only, gainers_only: false }))}
                />
                <FilterPill
                    label="High Volume"
                    icon={BarChart3}
                    active={filters.high_volume_only}
                    onClick={() => setFilters((p: any) => ({ ...p, high_volume_only: !p.high_volume_only }))}
                />
            </div>

            {Object.keys(filters).length > 0 && onReset && (
                <button
                    onClick={onReset}
                    className="px-4 py-2 text-sm font-medium text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 rounded-full transition-colors flex items-center gap-2 whitespace-nowrap"
                >
                    <X className="w-4 h-4" /> Clear Filters
                </button>
            )}
        </div>
    );
}
